.btn{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 68px;
  border-radius: 6px;
  transition: .4s;
  text-transform: uppercase;
  &.--enter{
    background-color: #DDFF54;
    padding-left: 24px;
    padding-right: 24px;
    &:hover{
      background-color: #01A700;
      color: #fff;
    }
  }
  &.--red{
    background-color: #F15858;
    min-width: 237px;
    color: #fff;
    padding-left: 26px;
    padding-right: 26px;
    &:hover{
      background-color: #922D69;
    }
  }
  &.--blue{
    min-width: 210px;
    padding-left: 22px;
    padding-right: 22px;
    background-color: #10ACC5;
    color: #fff;
    &:hover{
      background-color: #08716D;
    }
  }
}