.main{
  padding-top: 166px;
  padding-bottom: 276px;
  color: #fff;
  background: url(../img/main.svg) no-repeat center bottom / 1950px auto;
}

@media(min-width: 1950px){
  .main{
    background: url(../img/main.svg) no-repeat center bottom / 100% auto;
  }
}

.main__description{
  font-family: Calibri;
  font-size: 48px;
  line-height: 59px;
  margin-bottom: 26px;
}

.main__title{
  margin-bottom: 26px;
}

.main__text{
  max-width: 740px;
  margin-bottom: 52px;
}

.main__btns{
  .btn.--red{
    margin-right: 26px;
  }
}



.tasks {
  margin-top: 20px;
  margin-bottom: 38px;
}

.tasks__list{
  max-width: 1020px;
  margin: 0 auto -46px;
  justify-content: space-between;
}

.task{
  width: calc(50% - 10px);
  margin-bottom: 46px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.task__pic{
  width: 120px;
  height: 120px;
  border-radius: 50%;
  overflow: hidden;
}

.task__text{
  width: calc(100% - 152px);
}

.timer {
  background: url(../img/timer.svg) no-repeat center / 1950px auto;
  padding: 130px 0 110px;
  .btn.--red{
    margin: 0 auto;
  }
}

@media(min-width: 1950px){
  .timer{
    background: url(../img/timer.svg) no-repeat center bottom / 100% auto;
  }
}

.timer__block{
  display: flex;
  justify-content: center;
  max-width: 674px;
  box-shadow: 2px 4px 30px #79CDBD;
  border-radius: 20px;
  background-color: #fff;
  padding: 23px 0 26px;
  margin: 0 auto 44px;
  width: 100%;
  
}


.timer__item--center{
  padding-left: 102px;
  padding-right: 102px;
  position: relative;
  .timer__number{
    &:after,
    &::before{
      content: ":";
      position: absolute;
      top: 10px;
      font-weight: 600;
      font-size: 72px;
      line-height: 88px;
    }
    &:after{
      left: 100%;
      margin-left: 32px;
    }

    &:before{
      right: 100%;
      margin-right: 32px;
    }
  }
}

.timer__item{
  text-align: center;
}

.timer__number{
  color: #108DA8;
  font-weight: 600;
  font-size: 96px;
  line-height: 117px;
  font-family: $montserrat;
  position: relative;
}

.timer__text{
  font-size: 24px;
  line-height: 28px;
}


.calendar{
  padding-top: 15px;
}

.calendar__list{
  display: flex;
  justify-content: space-between;
}

.calendar__item{
  width: calc(33.333% - 10px);
}

.calendar__pic{
  margin-bottom: 35px;
  display: flex;
  justify-content: center;
}

.calendar__name{
  font-size: 30px;
  line-height: 35px;
  color: #108DA8;
  margin-bottom: 10px;
  text-align: center;
}

.calendar__text {
  font-size: 24px;
  line-height: 28px;
  text-align: center;
}


.organization{
  background: url(../img/organization.svg) no-repeat center top / 1950px auto;
  padding-top: 133px;
  padding-bottom: 111px;
  h2{
    margin-bottom: 70px;
  }
}

@media(min-width: 1950px){
  .organization{
    background: url(../img/organization.svg) no-repeat center top / 100% auto;
  }
}


.organization__list{
  display: flex;
  justify-content: center;
  margin-bottom: 83px;
}

.organization__item{
  margin-left: 30px;
  margin-right: 30px;
  width: 100%;
  max-width: 480px;
  box-shadow: 2px 4px 30px #73CFB7;
  border-radius: 20px;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 213px;
  padding-left: 40px;
  padding-right: 40px;
}

.organization__text{
  width: calc(100% - 160px);
  font-size: 20px;
}

.footer__logo{
  margin-right: 56px;
}

.socials__link{
  margin-right: 16px;
  &:last-child{
    margin-right: 0px;
  }
}

.footer__socials{
  margin-right: 36px;
}