.header{
  background-color: #7BCFBF;
  padding: 20px 0;
}

.header__logo{
  margin-right: 65px;
}

.nav__link{
  font-size: 18px;
  text-transform: uppercase;
  margin-right: 46px;
  color: #fff;
  position: relative;
  &:after{
    content: "";
    position: absolute;
    top: 100%;
    margin-top: 18px;
    width: 100%;
    height: 4px;
    background-color: #fff;
    left: 0;
    opacity: 0;
    transition: .4s;
  }
  &:hover{
    &:after{
      opacity: 1;
    }
  }
  &.active{
    &:after{
      opacity: 1;
    }
  }
  &:last-child{
    margin-right: 0px;
  }
}